body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.main_div{
  width: 100%;
  height: 100vh;
  background: #6983aa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

.center_div{
  width:25%;
  height: 80vh;
  background-color: #f4f4f4;
  -webkit-box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
          box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  border-radius: 15px; 
}

h1{
  color: white;
  background: transparent;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  -webkit-box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
          box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3)
}

input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size:20px;
  font-weight: 500;
  width: 60%;
  border-radius: 2px #8566aa;
  outline: none;
  
}
button:hover{
  background-color: green;
}

ol{
  margin-top: 30px;
}

.todo-style{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: left;
      justify-content: left;
  -ms-flex-align: center;
      align-items: center; 
}

.deleteButton{
  margin-right: 14px
}


